'use strict';

// MODULES //

import { factory as factory } from '@stdlib/ndarray-vector-ctor';


// MAIN //

/**
* Returns a clamped unsigned 8-bit integer vector (i.e., a one-dimensional ndarray).
*
* @name Uint8ClampedVector
* @type {Function}
* @param {(NonNegativeInteger|Collection|ArrayBuffer|Iterable)} [arg] - length, typed array, array-like object, buffer, or iterable
* @param {NonNegativeInteger} [byteOffset=0] - byte offset
* @param {NonNegativeInteger} [length] - view length
* @param {Options} [options] - function options
* @param {boolean} [options.readonly=false] - boolean indicating whether to return a read-only vector
* @param {string} [options.mode='throw'] - specifies how to handle indices which exceed vector dimensions
* @param {string} [options.order='row-major'] - memory layout (either row-major or column-major)
* @throws {TypeError} first argument must be either a length, typed array, array-like object, buffer, iterable, or options object
* @throws {TypeError} must provide valid options
* @returns {ndarray} one-dimensional ndarray
*
* @example
* import numel from '@stdlib/ndarray-numel';
*
* var arr = new Uint8ClampedVector();
* // returns <ndarray>
*
* var len = numel( arr );
* // returns 0
*
* @example
* import numel from '@stdlib/ndarray-numel';
*
* var arr = new Uint8ClampedVector( 2 );
* // returns <ndarray>
*
* var len = numel( arr );
* // returns 2
*
* @example
* import numel from '@stdlib/ndarray-numel';
*
* var arr = new Uint8ClampedVector( [ 1, 2 ] );
* // returns <ndarray>
*
* var len = numel( arr );
* // returns 2
*
* @example
* import numel from '@stdlib/ndarray-numel';
* import ArrayBuffer from '@stdlib/array-buffer';
*
* var buf = new ArrayBuffer( 32 );
* var arr = new Uint8ClampedVector( buf );
* // returns <ndarray>
*
* var len = numel( arr );
* // returns 32
*
* @example
* import numel from '@stdlib/ndarray-numel';
* import ArrayBuffer from '@stdlib/array-buffer';
*
* var buf = new ArrayBuffer( 32 );
* var arr = new Uint8ClampedVector( buf, 16 );
* // returns <ndarray>
*
* var len = numel( arr );
* // returns 16
*
* @example
* import numel from '@stdlib/ndarray-numel';
* import ArrayBuffer from '@stdlib/array-buffer';
*
* var buf = new ArrayBuffer( 64 );
* var arr = new Uint8ClampedVector( buf, 16, 2 );
* // returns <ndarray>
*
* var len = numel( arr );
* // returns 2
*/
var Uint8ClampedVector = factory( 'uint8c' );


// EXPORTS //

export default Uint8ClampedVector;
